<template>
  <b-card header="">
    <b-btn @click="resetCamera()"></b-btn>
    <div>
      (Z): {{ cameraTheta.toFixed(1) }}
      <b-form-input
        type="range"
        id="cameraTheta"
        :value="cameraTheta"
        min="0"
        max="180"
        @change="cameraThetaChanged($event)"
      />
    </div>
    <div>
      (X): {{ cameraPhi.toFixed(1) }}
      <b-form-input
        type="range"
        id="cameraPhi"
        :value="cameraPhi"
        min="0"
        max="360"
        @change="cameraPhiChanged($event)"
      />
    </div>
    <div>
      : {{cameraDis.toFixed(1)}}
      <b-form-input
        type="range"
        id="cameraDis"
        :value="cameraDis"
        min="1"
        max="180"
        @change="cameraDisChanged($event)"
      />
    </div>
    <b-checkbox v-model="sphereCoord"></b-checkbox>
  </b-card>
</template>

<script>
import {radToDeg} from '@/states'
export default {
  props: {
    camera: {
      type: Object,
      default: {
        theta: 0,
        phi: 0,
        dis: 4
      },
    }
  },
  data() {
    return {
      sphereCoord: true,
    }
  },
  computed: {
    cameraPhi() {
      return radToDeg(this.camera.phi);
    }, 
    cameraTheta() {
      return radToDeg(this.camera.theta);
    },
    cameraDis() {
      return this.camera.dis;
    },
  },
  methods: {
    cameraPhiChanged(value) {
      this.camera.rotate(null, value, null);
    },
    cameraThetaChanged(value) {
      this.camera.rotate(value, null, null);
    },
    cameraDisChanged(value) {
      this.camera.rotate(null, null, value);
    },
    resetCamera() {
      this.camera.reset()
    }
  }
};
</script>

